@import url(https://fonts.googleapis.com/css?family=Montserrat:300&display=swap);
*{padding:0;margin:0;box-sizing:border-box;font-family:"Playfair Display", serif}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

*{font-family:"Montserrat", serif;box-sizing:border-box}body{width:100vw;height:100vh;margin:0}#root{width:100vw;padding:0;margin:0}nav{background-color:aqua;font-size:20px}.home body{height:88vh}.home body img{width:100%;height:45vh}.home body .product-categories{margin:0 auto}.home body .product-categories h2{margin-left:50%}.home body .product-categories ul{display:flex;align-items:center;margin:auto 20% auto 20%}.home body .product-categories ul li{width:20vw;height:30vh;margin:10px;list-style:none;text-align:center;background-color:#c0d1b5;border-radius:10px;border:1px solid #ccc}.home body .product-categories ul li:hover{transition:box-shadow 0.3s;box-shadow:inset 0 0 50px #333}.home body .product-categories ul li img{width:100%;height:80%}@media (max-width: 500px){.home body .product-categories ul li img{display:flex;flex-flow:column wrap;max-width:100%;height:20vh}}@media (max-width: 500px){.home body{max-width:100%;height:auto}}.card-product{display:flex;flex-wrap:wrap;justify-content:center;margin:0 auto}.cartCard img{width:24rem}footer{margin:0 auto;padding-top:1%;text-align:center;min-height:50px;color:#0d0d0d;background-color:cornflowerblue}

